export default {
	data() {
		return {
			isdisable:1,
			id:'',
			page:1,
			pagesize:10,
			datalist:[]
		}
	},
	onLoad(){
		this.getdata()
	},
	onReachBottom() {
		this.page = this.page+=1
		console.log(this.page)
		this.getdata()
	},
	methods: {
		getimgurl(image){
			return"http://uniapp.ruange.com.cn/"+image
		},
		def(index){
			this.datalist[index].status=1
			uni.redirectTo({
				url:'../myhousetype/myhousetype'
			})
			uni.request({
				url:this.common.websiteUrl+"house_index_setdefhome",
				header:{"user-token":"6a109faf305513d443337ddb1ad4cb9b"},
				method:"post",
				data:{
					'id':this.datalist[index].id,
					'user_id':this.id
				},
				success: (res) => {
				}
			});
		},
		delitem(index){
			
			uni.request({
				url:this.common.websiteUrl+"house_index_delhome",
				header:{"user-token":"6a109faf305513d443337ddb1ad4cb9b"},
				method:"post",
				data:{
					'id':this.datalist[index].hometype,
					'user_id':this.id
				},
				success: (res) => {
					console.log(res)
					console.log(res.data.msg)
					this.datalist.splice(index, 1);
					this.$u.toast(`已删除`);
					uni.redirectTo({
						url:'../myhousetype/myhousetype'
					})
				}
			});
		},
		logoTime(index){
			this.datalist[index].del_is = true
			this.datalist.map((val, idx) => {
				if(index != idx) this.datalist[idx].del_is = false;
			})
		},
		back(){
			uni.switchTab({
			    url: '/pages/my/my'
			});
		},
		addhousetype(){
			uni.navigateTo({
			    url: '../myhousetypesearch/myhousetypesearch'
			});
		},
		callback(value){
			uni.showToast({
				title:value,
				icon:'none'
			})
		},
		getdata(){
			uni.getStorage({
				key:'userinfo',
				success:(res)=>{
					console.log('进来了')
					this.id=res.data.id
					uni.request({
						url:this.common.websiteUrl+"house_index_myhometype",
						header:{"user-token":"6a109faf305513d443337ddb1ad4cb9b"},
						method:"post",
						data:{
							'page':this.page,
							'pagesize':this.pagesize,
							'user_id':this.id
						},
						success: (res) => {
							if(res.data.code==200){
								// console.log(res.data.data)
								this.datalist = this.datalist.concat(res.data.data)
								console.log(this.datalist)
								this.isdisable=2
							}else{
								this.callback('暂无更多户型')
							}
						}
					});
					
				}
			})
			
		},
	}
}
